import Vue from 'vue';
import VueX from 'vuex';

Vue.use(VueX);

const LoginModule = {
  state: { 
    loggedIn: true
  },
  mutations: {
    
  },
  getters: {
    isLoggedIn (state) {
      return state.loggedIn
    }
  },
  actions: {
    
  }
}

export const store = new VueX.Store({
  modules: {
    LoginModule
  }
})


